.nav_item::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background-color: #004e97;
  background-color: var(--brand_color);
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transform-origin: center;
          transform-origin: center;
  transition: all 500ms;
}
.nav_item:hover::after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
